<template>
	<div class="shopCart">
		<!-- 底部内容 -->
		<div @click="showCart()" class="content">
			<div class="content-left">
				<div class="logo-wrapper">
					<div class="logo active">
						<i class="icon-shopping_cart"></i>
					</div>
					<div class="badge">{{totalCount}}</div>
				</div>
				<div class="price" :class="{'active':totalPrice}">￥{{totalPrice}}</div>
				<div class="desc">另需要配送费￥{{deliveryPrice}}元</div>
			</div>
			<div class="content-right" :class="{'enough':totalPrice>=minPrice}">
				<div class="pay">{{ownPrice}}</div>
			</div>
		</div>
		<!-- 购物车列表 -->
		<transition name="transHeight">
			<div v-show="show && totalPrice>0" class="shopcart-list" ref="shopCartList">
				<div class="list-header">
					<h1 class="title">购物车</h1>
					<span @click="clearCart()" class="empty">清空</span>
				</div>
				<div class="list-content" ref="listContent">
					<ul>
						<li class="food" v-for="item in food">
							<span class="name">{{item.name}}</span>
							<div class="price">{{item.price}}</div>
							<div class="cartcontrol-wrapper">
								<cartcontrol :foods="item"></cartcontrol>
							</div>
						</li>
					</ul>
				</div>
		</div>
		</transition>
	</div>
</template>
<script>
import Scroll from 'better-scroll'
import cartcontrol from './cartcontrol'
export default{
	data(){
		return {
			show:false
		}
	},
		   //配送费         起送费用     count大于0的商品
	props:['deliveryPrice','minPrice','food'],
	computed:{
		//总价
		totalPrice(){
			let price=0;
			this.food.forEach(val=>{
				if(val.count>0){
					price += val.count * val.price;
				}
			});
			return price;
		},
		//选择的商品总数
		totalCount(){
			let count=0;
			this.food.forEach(val=>{
				if(val.count>0){
					count += val.count;
				}
			});
			return count;
		},
		//差多少钱起送
		ownPrice(){
			if(this.totalPrice === 0){
				return `￥${this.minPrice}起送`
			}else if(this.totalPrice < this.minPrice){
				return `还差${this.minPrice - this.totalPrice}起送`
			}else{
				return `去结算`
			}
		}
	},
	methods:{
		clearCart(){
			this.food.forEach(val=>{
				val.count = 0;
			});
			this.show = false;
		},
		showCart(){
			this.show = !this.show;
			this.$nextTick(()=>{
				this.scroll = new Scroll(this.$refs.listContent,{click:true});
			})
		}
	},
	components:{
		cartcontrol
	}
}
</script>
<style scoped>
.shopCart{
	left:0;
}
</style>



