<template>
	<div class="goods">
		<!-- 左侧列表 -->
		<div class="menu-wrapper" ref="menuWrapper">
			<ul>
				<li class="menu-item" v-for='item in datas.goods'>
					<span class="text">
						<span  v-show="item.type>0" class="iconMap" :class="icon[item.type]"></span>{{item.name}}
					</span>
				</li>
			</ul>
		</div>
		<!-- 商品主页 -->
		<div class="foods-wrapper" ref="foodsWrapper">
			<ul>
				<li  v-for="item in datas.goods" class="food-list food-list-hook">
					<h1 class="title">{{item.name}}</h1>
					<ul>
						<li v-for="food in item.foods" class="food-item" @click="showDe(food)">
							<div class="icon">
								<img :src="food.icon" width="57" height="57" alt="icon">
							</div>
							<div class="content">
								<h2 class="name">{{food.name}}</h2>
								<p class="description">{{food.description}}</p>
								<div class="sell-info">
									<span class="sellCount">月售{{ food.sellCount}}</span>
									<span class="rating">好评率{{ food.rating}}</span>
									<div class="price">
										<span class="newPrice">￥{{food.price}}</span>
									</div>
									<div class="cartcontrol-wrapper">
										<cartcontrol :foods="food"></cartcontrol>
									</div>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopCart :food="food" :deliveryPrice="datas.seller.deliveryPrice" :minPrice="datas.seller.minPrice"></shopCart>
		<foodDetail ref="foodDetail" :foodDl="foodDl"></foodDetail>
	</div>
</template>
<script>
  import Scroll from 'better-scroll'
  import shopCart from './shopCart'
  import cartcontrol from './cartcontrol'
  import foodDetail from './foodDetail'
  export default {
	props:['datas'],
	data(){
		return {
			icon:['decrease','discount','special','invoice','guarantee'],
			foodDl:{}
		}
	},
	mounted(){
		this.$nextTick(() => {
			if(!this.scroll){
				this.scroll = new Scroll(this.$refs.foodsWrapper,{click: true});
			}else{
				this.scroll.refresh();
			};
			this.scrollA = new Scroll(this.$refs.menuWrapper,{click: true});
		})
	},
	components:{
		shopCart,
		cartcontrol,
		foodDetail
	},
	computed:{
		food(){
			let foodList=[];
			this.datas.goods.forEach(goods=>{
				goods.foods.forEach(val=>{
					if(val.count>0){
						foodList.push(val);
					}
				})
			});
			return foodList;
		}
	},
	methods:{
		showDe(val){
			this.foodDl = val;
			this.$refs.foodDetail.showFoods();
		}
	}
 }
</script>
<style>
li{
	cursor:pointer;
}
</style>


