<template>
	<!-- 商品详情 -->
	<transition name="move">
		<div class="detailWrapper" v-show="showDetail" ref="detailWrapper">
		<div class="foodDetail">
			<div class="back" @click="showDetail = !showDetail">
				<i class="icon-arrow_lift"></i>
			</div>
			<img :src="foodDl.image" height="425" width="100%" alt="detailPic">
			<div class="info">
				<div class="title">{{foodDl.name}}</div>
				<div class="desc">
					<span>月售{{foodDl.sellCount}}</span>
					<span>好评率{{foodDl.rating}}%</span>
				</div>
				<div class="price">￥{{foodDl.price}}</div>
				<transition name="fade">
					<div class="shopCart" @click="addcart()" v-show="foodDl.count === 0 || foodDl.count === undefined">
						<div class="text">加入购物车</div>
					</div>
				</transition>
				<cartcontrol ref="cartcontrol" :foods="foodDl"></cartcontrol>
			</div>
			<div class="divider"></div>
			<div class="desc">
				<div class="title">商品介绍</div>
				<div class="content">{{
				foodDl.info}}</div>
			</div>
				<div class="divider"></div>
				<div class="evaluation">
					<div class="title">商品评价</div>
					<div class="classify">
						<span class="item" v-for="( item , index ) in classify" :class="{'active': item.active,'bad': index == 2,'badActive': index == 2 && item.active }" @click="clickTab( item )">
							{{item.name}}
							<span class="count">{{ item.num }}</span>
						</span>
					</div>
					<div class="switch" @click=" commentsOnly = !commentsOnly ">
						<span class="icon-check_circle" :class="{'on':commentsOnly}"></span>
						<span class="text">只看有内容的评价</span>
					</div>
					<div class="evel-list">
						<ul>
							<li class="evel" v-for="list in selectComment">
								<div class="userInfo">
									<div class="time">{{ list.rateTime }}</div>
									<div class="user">
										<span>{{ list.username }}</span>
										<img :src=" list.avatar " width="12" height="12" alt=" userPic" >
									</div>
									
								</div>
								<div class="content">
									<span class="icon" :class="list.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
									<span class="text">{{ list.text }}</span>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>
<script>
	import cartcontrol from './cartcontrol'
	import Scroll from 'better-scroll'
	export default {
		props:['foodDl'],
		data(){
			return {
				showDetail:false,
				classify:[
					{name: '全部',num: 0,comments: [],active: true},
					{name: '推荐',num: 0,comments: [],active: false},
					{name: '吐槽',num: 0,comments: [],active: false}
				],
				commentsList: [],
				commentsOnly: false
			}
		},
		components: {
			cartcontrol
		},
		methods:{
			showFoods(){
				//显示页面
				this.showDetail = true;
				//获取并且存评论
				this.$nextTick(() => {
					this.commentsList = this.foodDl.ratings;
					this.foodDl.ratings.forEach( val => {
						this.classify[0].num++;
						this.classify[0].comments.push( val );
						if(val.rateType){
							this.classify[2].num++;
							this.classify[2].comments.push( val );
						}else{
							this.classify[1].num++;
							this.classify[1].comments.push( val );
						}
					});
				});
				//滚动插件点击还有重新刷新高度的问题
				this.$nextTick(() => {
					if(!this.scroll){
						this.scroll = new Scroll(this.$refs.detailWrapper,{click:true})
					}else{
						this.scroll.refresh;
					}
				});
			},
			addcart(){
				this.$refs.cartcontrol.addCart();
			},
			clickTab( val ){
				this.commentsList = val.comments;
				this.classify.forEach( val => {
					val.active = false;
				});
				val.active = true;
			}
		},
		computed:{
			selectComment(){
				let arrList = [];
				if( this.commentsOnly ){
					this.commentsList.forEach( val => {
						if( val.text.length > 0 ){
							arrList.push( val );
						}
					});
					return arrList
				} else {
					return this.commentsList
				}
			}
		}	
	}
</script>


