<template>
	<div class="star" :class=" 'star-' + size ">
		<span class="star-item" :class='item' v-for='item in xingxing'></span>
	</div>
</template>
<script>
export default {
	props:['score','size'],
	computed:{
		xingxing(){
			let arr = [];
			let zhengshu = Math.floor(this.score);
			let xiaoshu = (this.score-zhengshu)>=.5;
			for (let i=0;i<zhengshu;i++) {
				arr.push('on');
			}
			if(xiaoshu){
				arr.push('half');
			}
			while(arr<5){
				arr.push('off');
			}
			if(arr.length>=5){
				arr.push('on');
				arr.length = 5;
			}
			return arr;
		}
	}
}
</script>
<style>

</style>


