<template>
  <div id="app">
    <!-- 头部 -->
    <vheader :seller="datas.seller"></vheader>
    <div class="tab">
      <div class="tab-item">
        <router-link to='/goods'>商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to='/ratings'>评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to='/seller'>商家</router-link>
      </div>
    </div>
    <!-- 把加载的组件缓存 -->
    <keep-alive>
      <router-view :datas='datas' ref="abc"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import vheader from './components/header'
import axios from 'axios'
export default {
  name: 'App',
  data(){
      return {
        datas : {
          seller:{},
          goods : [],
          rattings : []
      }
    }
  },
  mounted(){
    axios.get('../static/data.json').then(res => {
      this.datas.seller = res.data.seller;
      this.datas.goods = res.data.goods;
      this.datas.rattings = res.data.ratings;
    }).catch(err => {
      console.log(err);
   });
    document.addEventListener('visibilitychange',this.titleChange,false)
  },
  components : {
    vheader
  },
  methods: {
    titleChange(){
      if(document.hidden){
        document.title = '期待下次点餐(T_T)';
      }else{
        document.title = '欢迎回来o(*￣▽￣*)ブ';
      }
    }
  }
}
</script>

<style scoped>
  .tab .tab-item .router-link-exact-active{
  color:red;
}
</style>



