<template>
	<!-- 加减号 -->
	<div class="cartcontrol">
		<transition name="fadeRotate">
			<div class="cart-decrease" @click.stop="decreaseCart()" v-show="foods.count>0">
			<span class="icon-remove_circle_outline inner"></span>
			</div>
		</transition>
		<div class="cart-count" v-show="foods.count>0">{{foods.count}}</div>
		<div class="cart-add" @click.stop="addCart()">
			<i class="icon-add_circle"></i>
		</div>
	</div>
</template>
<script>
import Vue from 'vue'
import {mapMutations,mapState} from 'vuex'
export default {
	props:['foods'],
	methods:{
		...mapMutations(['vxaddCart','vxdecreaseCart']),
		addCart() {
			if(typeof this.foods.count === "undefined"){
				Vue.set(this.foods,'count',0);
				Vue.set(this.foods,'active',true);
			}
			this.foods.count++;
			if(this.foods.active){
				this.vxaddCart(this.foods);
				this.foods.active = false;
			}
		},
		decreaseCart() {
			this.foods.count--;
			if(this.foods.count === 0){
				this.vxdecreaseCart(this.foods);
			}
		}
	}	
}
</script>


