<template>
	<div class="ratingsWrapper" ref="scrollList">
		<div class="ratings-content">
			<div class="info">
				<div class="mark">
					<div class="num">{{datas.seller.score}}</div>
					<div class="text">综合评分</div>
					<div class="contrast">高于周边商家{{datas.seller.rankRate}}%</div>
				</div>
				<div class="stars">
					<div class="serviceScore">
						<span class="text">服务态度</span>
						<star :score="datas.seller.serviceScore" :size="36"></star>
						<span class="num">{{datas.seller.serviceScore}}</span>
					</div>
					<div class="foodScore">
						<span class="text">送餐服务</span>
						<star :score="datas.seller.foodScore" :size="36"></star>
						<span class="num">{{datas.seller.foodScore}}</span>
					</div>
					<div class="deliveryTime">
						<span class="text">送达时间</span>
						<span class="time">{{datas.seller.deliveryTime}}</span>
					</div>
				</div>
			</div>
			<div class="divider"></div>
			<div class="evaluation">
				<div class="classify">
					<span class="item" :class="{'active': item.active,'bad':index===2,'badActive':item.active && index===2}" v-for="( item , index ) in classType" @click="tab( item )">
							{{item.name}}
						<span class="count">{{ item.count }}</span>
					</span>
				</div>
				<div class="switch" @click="clickShow()">
						<span class="icon-check_circle" :class="{'on':show}"></span>
						<span class="text">只看有内容的评价</span>
				</div>
				<div class="evel-list">
					<ul>
						<li class="evel" v-for="item in showComment">
							<div class="avatar">
								<img :src="item.avatar" alt="userPic" width="28" height="28">
							</div>
							<div class="content">
								<div class="user">
									<span class="name">{{item.name}}</span>
									<span class="rateTime">{{item.rateTime}}</span>
								</div>
								<div class="star-wrapper">
									<star :size="24" :score="item.score"></star>
									<span class="deliveryTime">{{item.deliveryTime}}分钟送达</span>
								</div>
								<div class="text">{{item.text}}</div>
								<div class="recommend">
									<span class="icon" :class="item.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
									<span class="dish" v-for="dish in item.recommend">{{dish}}</span>
								</div>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import star from './star.vue'
import axios from 'axios'
import Scroll from 'better-scroll'
export default {
	props:['datas'],
	data(){
		return {
			ratings: [],
			classType: [
				{name: '全部', active: true, count: 0, comment: []},
				{name: '推荐', active: false, count: 0, comment: []},
				{name: '吐槽', active: false, count: 0, comment: []}
			],
			show: false,
			commentLists: []
		}
	},
	components: {
		star
	},
	mounted() {
		axios.get('./static/data.json').then( res =>{
				this.ratings = res.data.ratings;
				this.commentLists = res.data.ratings;
				this.ratings.forEach( val => {
					this.classType[0].comment.push( val );
					this.classType[0].count++;
					if( val.rateType ){
						this.classType[2].comment.push( val );
						this.classType[2].count++;
					}else{
						this.classType[1].comment.push( val );
						this.classType[1].count++;
					}
					});
				});
				//当ratings评论赋值完
				if(!this.scrollB){
					this.$nextTick( () => {
						this.scrollB = new Scroll(this.$refs.scrollList,{click: true});
					})
				}else{
					this.scrollB.refresh();
				}
	},
	methods: {
		tab( item ){
			this.classType.forEach( val => {
				val.active = false;
			});
			this.commentLists = item.comment;
			item.active = true;
		},
		clickShow(){
			this.show = !this.show;
		}
	},
	computed: {
		showComment(){
			if( this.show ){
				let arr = [];
				this.commentLists.forEach( val => {
					if( val.text.length > 0 ){
						arr.push( val );
					}
				})
				return arr
			}else{
				return this.commentLists;
			}
		}
	}
}
</script>



